.product-large{
    width: 100%;
}
.form-login label{
    font-size: 12px;
    color: #333;
    font-weight: bold;
    letter-spacing: 2px;
}
.form-login .form-control {
    border-bottom: 1px solid #eee;
    padding: 5px 10px;
    max-width: 100%;
}
.settings-menu .menu-title h3 {
    border-bottom: 1px solid #e5e5e5;
    color: #7e7e7e;
    font-size: 16px;
    margin: 0 0 10px;
    padding: 5px 0;
    text-transform: uppercase;
}
.form-login .btn{
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 3px;
    width: 100%;
}
.form-login .form-control:focus {
    box-shadow: none;
}
.mainmenu ul.sub-menu {
    background: #ffffff none repeat scroll 0 0;
    box-shadow: 2px 0 7px 0 rgba(0, 0, 0, 0.13);
    left: 0;
    margin: 0;
    opacity: 0;
    padding: 30px;
    position: absolute;
    text-align: left;
    top: 86px;
    visibility: hidden;
    width: 215px;
    z-index: -9;
    transform: scaleY(0);
    transform-origin: 0 0 0;
    transition: all 0.5s ease 0s;
}
.mainmenu ul#nav li ul.sub-menu > li a, .mainmenu ul#nav li .megamenu span > a {
    border-bottom: 1px dotted #efefef;
    color: #444444;
    display: block;
    font-size: 12px;
    font-weight: 300;
    line-height: 30px;
    padding: 0;
    text-align: left;
    text-transform: capitalize;
}
.mainmenu ul#nav li .megamenu span > a:before, .mainmenu ul#nav li .sub-menu li > a:before {
    content: "";
    margin-right: 0px;
}
.mainmenu ul#nav li .megamenu{
    width: max-content;
    left: 0px;
}
.mainmenu ul#nav li .megamenu span {
    float: left;
    margin-right: 0px;
    padding: 15px;
    width: 185px;
}

@media (min-width: 992px)
{
    .container {
        max-width: 970px;
    }
}
@media (min-width: 1200px){
    .container {
        width: 1170px;
        max-width: 1170px;
    }
    .header-right-menu > ul > li {
        line-height: 96px;
        padding: 0 12px;
        position: relative;
    }
}