.title_block {
    border-bottom: 2px solid #363636;
    border-top: 2px solid #363636;
    color: #363636;
    font-size: 12px;
    font-weight: bold;
    margin: 0 0 20px;
    padding: 10px 0;
    text-transform: uppercase;
}
.sb a{
    color:#111 !important;
}
.sub-title {
    color: #cf5c7c !important;
    display: block;
    font-size: 13px;
    font-weight: bold;
    line-height: 30px;
}
.sub-title-black{
    color: #111 !important;
    display: block;
    font-size: 13px;
    font-weight: bold;
    line-height: 30px;
}
.sub-title i{
    border: 1px solid #f6f6f6;
    border-radius: 50%;
    color: #c8c8c8;
    float: right;
    font-size: 10px!important;
    font-weight: 300;
    height: 20px;
    left: inherit;
    line-height: 20px;
    position: inherit;
    text-align: center;
    top: inherit;
    vertical-align: middle;
    width: 20px;
    display: none;
}
.sub-title .fa-plus{
    display: block;
}
.sub-title.active .fa-minus{
    display: block;
}
.sub-title.active .fa-plus{
    display: none;
}
