.item-bottom h5{
    color: #383c45;
    font-size: 13px;
    line-height: 20px;
    margin-bottom: 0;
    font-weight: bold;
}
.price{
    color: #363636;
    display: inline-block;
    font-size: 16px;
    font-weight: 500;
}
.item-top{
    position: relative;
}
.item-top:before{
    position: absolute;
    width: 100%;
    height: 100%;
    background: #cccccc70;
    content: " ";
    opacity: 0;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transition: all 300ms ease 0s;
}
.p-list:hover .item-top:before{
    opacity: 1;
}