.right-content-edit .form-control, .right-content .form-control{
    width: 100%;
    max-width: 100%;
    border: 1px solid #ccc;
    border-radius: 0px;
    padding: 5px 10px;
    height: 35px;
    background: #fbfbfb none repeat scroll 0 0;
}
.right-content-edit h2, .right-content h2{
    border-bottom: 1px solid #e5e5e5;
    color: #3f3f3f;
    font-size: 32px;
    margin: 0 0 20px;
    padding-bottom: 10px;
    text-transform: uppercase;
    width: 100%;
}
.right-content-edit label, .right-content label{
    color: #111;
}

.react-autosuggest__suggestions-container--open, .sugest {
    border: 1px solid #ccc;
    max-height: 150px;
    overflow-y: auto;
    position: absolute;
    width: calc(100% - 30px);
    background: #fff;
    z-index: 10;
}
.react-autosuggest__suggestions-container--open, .sugest li{
    padding: 8px 15px;
}
.react-autosuggest__suggestions-container--open, .sugest li:hover{
    background: #f5f5f5;
}

.mw-100{
    max-width: 50px !important;
}

.b-order{
    font-size: 14px;
}

.btn-submit{
    background: #282828 none repeat scroll 0 0;
    border: medium none;
    color: #fff;
    font-size: 17px;
    font-weight: 600;
    height: 47px;
    margin: 0 0 20px 0;
    padding: 0;
    text-transform: uppercase;
    transition: all .3s ease 0s;
    width: 100%;
    border-radius: 0px;
}
.t-default{
   color: #777777;
}
.page-link {
    position: relative;
    display: block;
    padding: .5rem .75rem;
    margin-left: -1px;
    line-height: 1.25;
    color: #777;
    background-color: #fff;
    border: 1px solid #dee2e6;
}
.page-link:hover, .page-link:hover i {
    color: #111 !important;
}